.App {
    text-align: center;
    height: 100%;
    width: 100%;
}

@import '~antd/dist/antd.css';
.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.demo-showing {
    animation: show 0.5s forwards;
}

.demo-fading {
    animation: fade 0.5s forwards;
}

@keyframes show {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fade {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

a,
article,
aside,
b,
body,
button,
dd,
div,
dl,
dt,
figcaption,
figure,
footer,
h1,
h2,
h3,
h4,
h5,
h6,
header,
i,
input,
li,
nav,
p,
section,
select,
span,
textarea,
ul {
    padding: 0;
    margin: 0;
    list-style: none;
    font-style: normal;
    text-decoration: none;
    border: none;
    /* color: #333; */
    font-weight: 400;
    font-family: Microsoft Yahei;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    -webkit-font-smoothing: antialiased
}

a:hover,
article:hover,
aside:hover,
b:hover,
body:hover,
button:hover,
dd:hover,
div:hover,
dl:hover,
dt:hover,
figcaption:hover,
figure:hover,
footer:hover,
h1:hover,
h2:hover,
h3:hover,
h4:hover,
h5:hover,
h6:hover,
header:hover,
i:hover,
input:hover,
li:hover,
nav:hover,
p:hover,
section:hover,
select:hover,
span:hover,
textarea:hover,
ul:hover {
    outline: none
}

::-webkit-scrollbar {
    width: 0;
    height: 0;
    background-color: #f5f5f5
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 1px transparent;
    border-radius: 10px;
    background-color: #f5f5f5
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: #555
}

input[type=button],
input[type=reset],
input[type=search],
input[type=submit],
textarea {
    -webkit-appearance: none
}

body,
html {
    height: 100%;
    width: 100%;
    background-color: #f5f5f5
}

.clear:after {
    content: "";
    display: block;
    clear: both
}

.clear {
    zoom: 1
}

.back_img {
    background-repeat: no-repeat;
    background-size: 100% 100%
}

.margin {
    margin: 0 auto
}

.left {
    float: left
}

.right {
    float: right
}

.hide {
    display: none
}

.show {
    display: block
}

.ellipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap
}

.paddingTop {
    padding-top: 1.95rem
}

@keyframes backOpacity {
    0% {
        opacity: 1
    }
    25% {
        opacity: .5
    }
    50% {
        opacity: 1
    }
    75% {
        opacity: .5
    }
    to {
        opacity: 1
    }
}

.animation_opactiy {
    animation: backOpacity 2s ease-in-out infinite
}

.router-fade-enter-active,
.router-fade-leave-active {
    transition: opacity .3s
}

.router-fade-enter,
.router-fade-leave-active {
    opacity: 0
}